.aboutus {
  background-color: #70005F;
  width: 100%;
  height: 100%;
  align-items: center;
  padding: 1rem 0 4rem 0;
}

.aboutus h1 {
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
}

.aboutus h2 {
  padding: 1rem;
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 23px;
}

.aboutus__cards {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
