.reports {
  background-color: #005488;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 20rem;
  align-items: center;
  margin: 1rem auto;
  padding-bottom: 5rem;
}

.reports h1 {
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
}

.reports h2 {
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 23px;
}
